export default "<template>\n  <Sandbox\n    id=\"example\"\n    title=\"Example\"\n  >\n    <Preview>\n      <VNotificator\n        :items=\"items\"\n        :round=\"round\"\n        :rounded=\"rounded\"\n        :rounded-lg=\"roundedLg\"\n        :size=\"size\"\n        :width=\"width\"\n        @remove-notification=\"removeNotification\"\n      />\n      <VButton\n        class=\"has-margin-top-lg\"\n        color=\"primary\"\n        rounded\n        dark\n        @click=\"addNotification\"\n      >\n        Add notification\n      </VButton>\n    </Preview>\n    <template #controls>\n      <Knobs>\n        <KnobBoolean\n          v-model=\"rounded\"\n          label=\"Rounded\"\n        />\n        <KnobBoolean\n          v-model=\"roundedLg\"\n          label=\"Rounded large\"\n        />\n        <KnobBoolean\n          v-model=\"round\"\n          label=\"Round\"\n        />\n        <KnobListItem\n          v-model=\"size\"\n          :items=\"sizes\"\n          label=\"Size\"\n        />\n        <KnobNumber\n          v-model=\"width\"\n          label=\"Width\"\n        />\n      </Knobs>\n    </template>\n  </Sandbox>\n</template>\n\n<script>\nimport {\n  Preview,\n  Sandbox,\n  Knobs,\n  KnobListItem,\n  KnobBoolean,\n  KnobNumber\n} from '@/docs/components';\nimport {\n  VNotificator,\n  VButton\n} from '@/components';\nimport { sizes } from '@/docs/helpers/story-params';\n\nexport default {\n  name: 'NotificatorExample',\n  components: {\n    Preview,\n    Sandbox,\n    Knobs,\n    KnobListItem,\n    KnobBoolean,\n    KnobNumber,\n    VNotificator,\n    VButton\n  },\n  data () {\n    return {\n      round: false,\n      rounded: true,\n      roundedLg: false,\n      width: null,\n      size: 'sm',\n      sizes,\n      types: [\n        'default',\n        'success',\n        'info',\n        'warning',\n        'error'\n      ],\n      items: [\n        {\n          id: '1',\n          message: 'Success!',\n          type: 'success'\n        },\n        {\n          id: '2',\n          message: 'Something went wrong!',\n          type: 'error'\n        },\n        {\n          id: '3',\n          message: 'Are you sure?',\n          type: 'warning'\n        },\n        {\n          id: '4',\n          message: 'Network error!',\n          type: 'error'\n        }\n      ],\n      id: 4\n    };\n  },\n  methods: {\n    removeNotification (notification) {\n      this.items = this.items.filter(i => i.id !== notification.id);\n    },\n    addNotification () {\n      this.id++;\n      this.items.push({\n        id: this.id,\n        message: 'Lorem ipsum...',\n        type: this.types[Math.floor(Math.random() * this.types.length)]\n      });\n    }\n  }\n};\n</script>\n\n<style lang=\"scss\">\n</style>\n";